.loginPage {
  height:100%;
  
}

.login-container {
  display: table;
  min-width: 100%;

}

.login-title {
  
  color: #fff;
  font-size: 3rem;
  font-weight: 500;
  margin-top: 8rem;
}

.form-content {
  display:table-cell;
  text-align: center;
  vertical-align: middle;
}

form {
  margin:auto;
  --text-color: #fff;
  max-width: 500px;
  background-color: none;
}

.field {
  width: 90%;
  position: relative;
  border-bottom: 2px dashed var(--text-color);
  margin: 5rem auto 1.2rem;
}

.label {
  display: block;
  color: var(--text-color);
  font-size: 1.2rem;
  z-index: -1;
  position: absolute;
  transform: translateY(-2rem);
  transform-origin: 0%;
  transition: transform 400ms;
}

.input {
  outline: none;
  border: none;
  overflow: hidden;
  margin: 0;
  width: 100%;
  padding: 0.25rem 0;
  background: none;
  color: #fff;
  font-size: 1.2rem;
  font-weight: 500;

  &:not(:placeholder-shown) + .label {
    transform: scale(0.8) translateY(-5rem);
  }
}
/*
.input:valid {
  color:#00BFA5;
}

.input:invalid {
  color:orangered;
}
*/
.field::after {
  content: "";
  position: relative;
  display:block;
  height:4px;
  width: 100%;
  background: #fff;
  transform: scaleX(0);
  transform-origin: 0%;
  transition: transform 500ms ease;
  top: 2px;
}

.field:focus-within {
  border-color: transparent;

  .label {
    transform: scale(0.8) translateY(-5rem);
  }

  &::after {
    transform: scaleX(1);
  }
}

.register-link {
    
    text-decoration: none;
    span {
        color: #fff;
        text-transform: uppercase;
        font-weight: 500;
        
    }
}

.btn {
  font-size: 1.2rem;
  font-weight: 500;
  outline: 0;
  border: 0;
  width: 30%;
  padding: 0.3rem;
  margin-bottom: 1rem;
  cursor: pointer;
  background: #fff;
 // color: $fontcolor;
  text-transform: uppercase;
}

